<template>
	<view class="ticketBalance">
		<label  v-if="transferList.length!=0">
			<checkbox value="cb" :checked="seletedAll" @tap="_seletedAll" class="checkbox" style="transform:scale(0.7)" color="#F05444" />全选
		</label>

		<view class="ticket-transfer-content" style="margin-top: 0rpx;margin-bottom: 50rpx;">
			<checkbox-group @change="seletedStatus" > 
			   <view class="transfer-box" v-for="item in transferList">
			   	<label >
			   		<checkbox     :value="item.id" class="checkbox" :checked="checkList.includes(item.id)" style="transform:scale(0.7)"
			   		 color="#F05444" />
			   	</label>
			   	<view class="transfer-check" @click="openpop">
			   		<view class="tc-left">
						<text class="t3">票券号:{{item.id}}</text>
			   			<text class="t1">演唱会专用票券</text>
			   			<text class="t2">有效期至2020-05-29</text>
			   		</view>
			   		<view class="tc-right">
			   			立即使用
			   		</view>
			   	</view>
			   </view>
			   		
			</checkbox-group>
			
		</view>
		<view class="none" v-if="transferList.length==0">
			<image src="../../static/none.png" mode=""></image>
			<view>暂无数据</view>
		</view> 
		<view class="transfer-btn-box"  v-if="transferList.length!=0">
			<button class="transfer-btn" @click="tansfer"  open-type="share">
				立即转让
			</button>
		</view>
	</view>
</template>

<script>
	import {
		API
	} from '@/util/api.js';
	export default {
		data() {
			return {
				transferList: [],
				checkList: [],
				seletedAll: false,
				adapte_active:0
			}
		},
		onLoad(option) {
			console.log(option.id)
			this.number = option.id
			this.getData();
			this.trans(1)
		},
		onShow() {

		},
		onShareAppMessage(res) {
			console.log(res)
			var checkList=JSON.stringify(this.checkList)
			    return {
			      title: "送你"+this.checkList.length+"张2021力王争霸中国巡回赛的兑换票券，请立即领取",
			      path: '/pages/index/index?id='+checkList+'&adapte_active='+this.adapte_active,
				  imageUrl:"https://igoleme.oss-cn-shenzhen.aliyuncs.com/5f6078d8af64a.png" 
			    }
		},
		methods: {
			tansfer(){
				 this.request(API.isSend, 'post', {
				 	tickRoll_id: this.checkList
				}).then(res => {
				 	console.log(res)
				 })
			},
			seletedStatus(e) {
				console.log(e)
				this.checkList = e.detail.value
				console.log(this.checkList)
				if (this.checkList.length === this.transferList.length) {
					this.seletedAll = true
				}else{
					this.seletedAll = false
				}
			},
			_seletedAll() {
				if (!this.seletedAll) {
					this.seletedAll = true;
					this.transferList.map(item => {
						this.checkList.push(item.id)
					 });
					console.log(this.checkList);
					console.log('true');
				} else {
					this.seletedAll = false
					this.checkList = []
					console.log(this.checkList)
					console.log('false')
				}
			},
			openpop() {
				console.log("tan")
			},
			hello() {
				uni.showToast({
					title: '只能赠送一次',
					duration: 2000
				})
			},
			trans(type) {
				this.request(API.getUserTickRollList, 'GET', {
					type: type
				}).then(res => {
					console.log(res)
					if (res.code === 10000) {
						this.titketype = res.data
						this.transferList = res.data.listData
						console.log(this.transferList)
					}
				});
			},
			getData() {
				//获取明星列表
				this.request(API.getList, 'GET', {
					limit: 10,
					page: 1
				}).then(res => {
					console.log(res)
					if (res.code === 10000) {
						this.list = res.data;
					}
				});
			},
			goUrl(id) {
				uni.navigateTo({
					url: '/pages/ticket/exchange?' + 'id=' + id + '&&adapte_active=0'
				});
				console.log(id)
			}
		}

	}
</script>

<style lang="scss">
	@import '../../assets/css/ticket/ticket.scss';
</style>
